import React from 'react';
import Navbar from '../../Navbar';
import CardItem from '../../product-card/CardItem';
import './card-service.css'
import cafet from './img/brightcafet.JPG'
import livraisons from './img/livraisons.jpg'
import mae from './img/brightmae.JPG'
import naturo from './img/naturo.JPG'
import Footer from '../../footer-import/containers/footer'
export default function Services() {
  return (
    <>
    <Navbar />
  <div className='global-container'>
    <div className='cards'>
    <h1 className='Services'></h1>
          <ul className='cards__items'>
            <CardItem
              src={cafet}
              text='Cafeteria'
              label=''
              path='/cafet'
            />
            <CardItem
              src={mae}
              text='Les paniers de Mae'
              label=''
              path='/Paniersdemae'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src={naturo}
              text='Bien-etre'
              label=''
              path='/naturopathe'
            />
            <CardItem
              src={livraisons}
              text='Livraisons et commandes'
              label='En Maintenance'
            />
          </ul>
    </div>
  </div>
  <Footer />
    </>
  );
}