import React from 'react';
import './App.css';
import Home from './components/pages/home-folder/Home';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Services from './components/pages/services-folder/Services';
import Vins from './components/pages/Vins-folder/vin-rouge';
import Histoire from './components/pages/Histoire-folder/Histoire';
import Pains from './components/pages/pain-folder/pain';
import Vrac from './components/pages/vrac-folder/Vrac';
import FruitsLegumes from './components/pages/fruits-legumes-folder/FruitsLegumes';
import Cafet from './components/pages/cafeteria-folder/cafeteria';
import Produit from './components/pages/produit-folder/produit';
import Naturopathe from './components/pages/services-folder/naturopathe-folder/naturopathe'
import Livraisons from './components/pages/services-folder/livraisons-folder/livraisons';
import PaniersdeMae from './components/pages/mae-folder/paniers-mae';
import VinRouge from './components/pages/Vins-folder/vin-rouge'
import VinBlanc from './components/pages/Vins-folder/vin-blanc'
import Champagne from './components/pages/Vins-folder/champagne'
import AutreVins from './components/pages/Vins-folder/autres'
import VinRose from './components/pages/Vins-folder/vin-rose'
import Contact from './components/pages/contact-folder/contact'
import PetitPrix from './components/pages/petits-prix-bio/petitprix'
import PPcosmetique from './components/pages/petits-prix-bio/pp-cosmetique'
import Epicerie from './components/pages/epicerie/epicerie'
import SoinCorps from './components/pages/soin-corps/soincorps'

function App() {
  return (
    <Router>
      <Switch>
        <Route path='/' exact component={Home} />
        <Route path='/services' component={Services} />
        <Route path='/Histoire' component={Histoire} />
        <Route path='/Pains' component={Pains} />
        <Route path='/Vins' component={Vins} />
        <Route path='/Vrac' component={Vrac} />
        <Route path='/FruitsLegumes' component={FruitsLegumes} />
        <Route path='/services' component={Services} />
        <Route path='/cafet' component={Cafet}/>
        <Route path='/Produit' component={Produit}/>
        <Route path='/naturopathe' component={Naturopathe}/>
        <Route path='/livraisons' component={Livraisons}/>
        <Route path='/Paniersdemae' component={PaniersdeMae}/>
        <Route path='/vinrouge' component={VinRouge}/>
        <Route path='/vinblanc' component={VinBlanc}/>
        <Route path='/champagne' component={Champagne}/>
        <Route path='/autrevins' component={AutreVins}/>
        <Route path='/vinrose' component={VinRose}/>
        <Route path='/contact' component={Contact}/>
        <Route path='/epicerie' component={Epicerie}/>
        <Route path='/petitprix' component={PetitPrix}/>
        <Route path='/ppcosmetique' component={PPcosmetique}/>
        <Route path='/soincorps' component={SoinCorps}/>
      </Switch>
    </Router>
  );
}

export default App;