import React from 'react'
import { Parallax } from 'react-parallax';
import inlineStyle from './vrac-parallax.css';
import vracbg from '../vrac-img/vrac-bg.JPG'



function ImgParallax() {
    return (
      <div style={{ textAlign: 'center' }}>
        <Parallax bgImage={vracbg} strength={500}>
          <div style={{ height: 500 }}>
            <div className="parallax-text" style={inlineStyle}></div>
          </div>
        </Parallax>
        </div>
    )
}

export default ImgParallax;