import React from 'react';
import Navbar from '../../Navbar';
import Footer from '../../footer-import/containers/footer'
import vrac1 from './vrac-img/vrac1.JPG'
import vrac3 from './vrac-img/vrac2.JPG'
import vrac2 from './vrac-img/vrac3.JPG'
import Vracparallax from './vrac-parallax/vrac-parallax'
import './vrac.css'

export default function pagecafeteria() {
    return(
        <>
        <Navbar />
        <Vracparallax/>

<div className='page-container-vrac'>
        <div className='tampoon'>.</div>
        <div className='title-vrac'>
          <h2>Le rayon vrac</h2>
        </div>
    <div className='content-container-vrac'>    
        <div className='vrac-paragraph'>
          <h2 className='vrac-titre'>Le rayon vrac La Vie Claire</h2>
          <p className='balise-p-vrac'>
            Dans un souci d'éviter au maximum les emballages devenus une des premières sources de pollution dans le monde,
            le rayon vrac de La Vie Claire Beauvais vous permet parmi un large choix de produits issus de l'agriculture Biologique,
            De choisir Vous même vos produits ainsi que leurs quantités, tout en vous permettant de ramener vos propres contenants et d'utiliser
            des sachets 100% recyclables fournis par le magasin.
          </p>
          <p className='balise-p-vrac'>
            La Vie Claire Beauvais met également à votre disposition des machines vous permettant de créer votre propre pâte à tartiner entièrement bio !
            en vous permettant de ramener vos propres contenants et de choisir entre la pâte à tartiner chocolat noisette, ou chocolat noix de cajou.
          </p>
        </div>
        <div className='vrac-img-container'>
              <img src={vrac1} alt=''/>
              <img src={vrac2} alt=''/>
              <img src={vrac3} alt=''/>
              <img src={vrac1} alt=''/>
        </div>

        <div className='vrac-paragraph'>
        </div>
    </div>
</div>
        <Footer />
        </>
    )
}
