import React from 'react';
import Slider from '../../slider/Slider'
import earth from './home-img/heart.svg'
import plant from './home-img/plant.svg'
import bio from './home-img/bio.svg'
import '../../../App.css'
import './Home.css'
import './Home-cards.css'

import Navbar from '../../Navbar'
import ImgParallax from '../Homeparalax/parallax'
import Footer from '../../footer-import/containers/footer'
import HomeCardItem from './HomeCardItem'
import AutoSlider from '../../../components/auto-slider/Slider'

import Pain from './home-img/IMG_9768.JPG'
import vins from './home-img/modified_IMG_9729.JPG'
import fruitlegumes from './home-img/IMG_9775.JPG'
import vrac from './home-img/IMG_9717.JPG'
import produitmoment from './home-img/promo.JPG'
import Pub2 from './home-img/pub1.png'
import Pub1 from './home-img/pelin.jpg'
import HomeVideo from './video-player-home/video-home'


export default function Home() {
  return (
    <>
    <Navbar />
    <AutoSlider />
<div className='homepage-container'>
    <div className='labelContainer'>
      <div className='earthlogo'>
        <img src={earth} alt=""/>
        <h3>La bio respectueuse des hommes et de l'environement</h3>
      </div>
      <div  className='biologo'>
        <img src={bio} alt=""/>
        <h3>L'exigence qualité au dela des normes</h3>
      </div>
      <div className='plantlogo' >
        <img src={plant} alt=""/>
        <h3>Des actions pour developper les fillières bio</h3>
      </div>
    </div>
    <ImgParallax />

    <div className='home-pub-container'>
      <a href="https://www.lavieclaire.com/la-vie-claire/nos-actions/les-projets-innovants-et-durables-soutenus-par-la-vie-claire">
        <img src={Pub1} alt=''/>
      </a>  
    </div>

    <div className='cards__home'>
    <div className='cards__home'>
      <div className='cards__container__home'>
        <div className='cards__wrapper__home'>
          <ul className='cards__items'>
            <HomeCardItem
              src={Pain}
              text='Nos Pains'
              label=''
              path='/Pains'
            />
            <HomeCardItem
              src={vins}
              text='Nos Vins Bio'
              label=''
              path='/Vins'
            />
          </ul>
          <ul className='cards__items'>
            <HomeCardItem
              src={produitmoment}
              text='Produits en promotion'
              path='/petitprix'
            />
            <HomeCardItem
              src={vrac}
              text='Le rayon Vrac'
              label=''
              path='/Vrac'
            />
            <HomeCardItem
              src={fruitlegumes}
              text='Nos Fruits et légumes'
              label=''
              path='/FruitsLegumes'
            />
          </ul>
        </div>
      </div>
    </div>
  </div>
    <div className='home-pub-container'>
      <a>
        <img src={Pub2} alt=''/>
      </a>  
    </div>
  <HomeVideo/>
</div>
<div>
  <h2 className='magasin-image'>Découvrez notre magasin en image</h2>
</div>
    <Slider/>

    <div className='home-paragraph-container'>
      <p className='home-p-paragraph'>Esprit libre, éclairé et pionnier de l’alimentation, Henri-Charles Geffroy a créé la revue « La Vie Claire » en 1946. 
        C’est deux ans plus tard en 1948 que le premier magasin biologique La Vie Claire a vu le jour. A cette occasion, 
        les premiers produits à la marque La Vie Claire ont été commercialisés dans ce-même magasin.
      </p>
      <p className='home-p-paragraph'>
        Au fil des années, notre enseigne s’est positionnée comme le magasin spécialiste de la bio, libre et indépendant. 
        Un état d’esprit que nous partageons avec nos clients, des consommateurs avisés, conscients des enjeux environnementaux, 
        sociétaux, économiques et déterminés eux aussi à faire valoir leurs choix.
      </p>
    </div>

    <Footer />

    </>
  );
  };
